.main-layout {
  background: powderblue;
  position: relative;
  height: 100vh;
  display: grid;
  grid-gap: 0;
  grid-template-rows: calc(100% - 56px) 56px;
  grid-template-columns: 100%;
  grid-template-areas:
    "c"
    "f";
}

.main-layout > .main-content {
  grid-area: c;
  display: flex;
  color: white;
  font-weight: bold;
  overflow: hidden;
  /* margin-top: 58px; */
}

.main-layout > .main-content > div,
.main-layout > .main-content > div > div {
  width: 100%;
  height: 100%;
}

.main-layout > .main-footer {
  grid-area: f;
}

.profile-screen {
  z-index: 9999;
  position: absolute;
  top: 0;
  /* bottom: 56px; */
  left: 0;
  width: 100%;
  pointer-events: none;
  /* -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease; */
  
  -moz-animation: all 200ms 1 linear;
  -webkit-animation: all 200ms 1 linear;
  -o-animation: all 200ms 1 linear;
}

.profile-screen.banner-mode {
  height: 58px;
  /* animation: profile-page-change; */
  animation: profile-page-change 200ms 1 linear;
  /* -moz-animation: all 150ms 2 linear;
  -webkit-animation: all 150ms 2 linear;
  -o-animation: all 150ms 2 linear;
  animation-iteration-count: 1;
   */
}
